.kijken {
  position: absolute;
  top: 30%;
  font-size: 3rem;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-name: ticker;
  animation-duration: 10s;

}

.schildersoog {
  position: absolute;
  top: 70%;
  font-size: 3rem;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-name: ticker;
  animation-duration: 20s;

}
/*FIXME: starts in the middle of the page*/
.beeldenmaker {
  position: absolute;
  font-size: 3rem;
  top: 10%;
  right: 50%;
  /*left:0%;*/
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-name: ticker;
  animation-duration: 20s;
}

.spelen {
  position: absolute;
  top: 50%;
  right: 50%;
  font-size: 3rem;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-name: ticker;
  animation-duration: 13s;
}

@keyframes ticker {
  0% { transform: translate3d(200%, 0, 0); }
  100% { transform: translate3d(-100%, 0, 0); }
}

.wrapper {
    overflow: hidden;
}
.sliding-background {
    background: url(/static/media/Homepageimage.527a9df6.jpg) repeat-x;
    height: 510px;
    width: 4000px;
    animation: slide 30s linear infinite;
}



@keyframes slide{
    0%{
        transform: translate3d(-1692px, -10px, 0);
    }
    100%{
        transform: translate3d(0, -10px, 0);
    }
}

